.table-container {
  overflow: hidden;
  margin: auto;
  padding: 20px;
}

.table-container.mobile {
  width: 100%;
  padding: 10px;
}

.table-container.desktop {
  max-width: 1200px; /* Adjust this value as needed */
  padding: 20px;
}

.MuiDataGrid-root {
  width: 100% !important;
}

.MuiDataGrid-columnHeaderTitle {
  font-size: 0.875rem; /* Adjust font size for better readability on small screens */
}

.MuiDataGrid-cell {
  font-size: 0.75rem; /* Adjust font size for better readability on small screens */
}

.MuiButton-root {
  padding: 2px 10px;
  font-size: 0.75rem;
  border-radius: 12px;
}
