.appointmentContainer {
    margin-left: 250px;
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.appointment-header {
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    text-transform: uppercase;
    top: 30px;
    margin-top: 50px;
}

.appointment-header span {
    font-size: 1.2rem;
    margin-right: 5px;
}

.today-appointment-text {
    color: #007bff; /* Blue color for "My Patients" */
    font-size: 1.2rem;
    font-weight: bold;
}

.appointment-table-container {
    width: 100%;
    overflow-x: auto;
    border-radius: 8px;
    margin-top: 10px;
    padding: 10px;
}


.shimmer-wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .shimmer-box {
    height: 100px; /* Adjust height as needed */
    background: linear-gradient(
      90deg,
      #f0f0f0 25%,
      #e0e0e0 50%,
      #f0f0f0 75%
    );
    background-size: 200% 100%;
    animation: shimmer 1.5s infinite;
    border-radius: 4px;
  }
  
  @keyframes shimmer {
    0% {
      background-position: 200% 0;
    }
    100% {
      background-position: -200% 0;
    }
  }  



@media (max-width: 768px) {
    .appointmentContainer {
        display: flex;
        flex-direction: column;
        padding: 20px;
        margin-left: 0px;
    }


    .appointment-header {
        display: flex;
        align-items: center;
        font-size: 1.2rem;
        text-transform: uppercase;
        top: 30px;
        margin-top: 70px;
    }
  

    .appointment-header span {
        font-size: 0.8rem;
        margin-right: 5px;
    }

  }

  