.patient-container {
    display: flex;
    flex-direction: column;
    padding: 20px;
    margin-left: 250px;
}

.dashboard-title {
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    text-transform: uppercase;
    top: 30px;
    margin-top: 50px;
}

.dashboard-title span {
    font-size: 1.2rem;
    margin-right: 5px;
}

.my-patients {
    color: #007bff;
    font-size: 1.2rem;
    font-weight: bold;
}

.table-container {
    width: 100%;
    overflow-x: auto;
    border-radius: 8px;
    margin-top: 10px;
    padding: 10px;
}


.shimmer-wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.shimmer-box {
    height: 100px;
    background: linear-gradient(90deg,
            #f0f0f0 25%,
            #e0e0e0 50%,
            #f0f0f0 75%);
    background-size: 200% 100%;
    animation: shimmer 1.5s infinite;
    border-radius: 4px;
}

@keyframes shimmer {
    0% {
        background-position: 200% 0;
    }

    100% {
        background-position: -200% 0;
    }
}


@media (min-width: 768px) {
    .table-container {
        width: auto;
        margin-left: 20px;
        margin-right: 20px;
    }
}

@media (max-width: 768px) {
    .patient-container {
        display: flex;
        flex-direction: column;
        padding: 20px;
        margin-left: 0px;
    }


    .dashboard-title {
        display: flex;
        align-items: center;
        font-size: 1.2rem;
        text-transform: uppercase;
        top: 30px;
        margin-top: 70px;
    }

    .dashboard-title span {
        font-size: 0.8rem;
        margin-right: 5px;
    }



}